/* Header.css */
.header-container {
    position: relative;
    width: 1280px;
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 25px;
}

.header {
    width: 100%;
    display: flex;
    justify-content: space-between; /* 양쪽 끝에 배치 */
    align-items: center;
}

.header-left {
    display: flex;
    align-items: center;
}

.logo {
    width: 120px;
}

.main-nav {
    margin-top: 110px;
}

.main-nav ul {
    display: flex;
    list-style: none;
    padding: 0;
    margin-top: 0; /* ul 요소의 위쪽 여백을 0으로 설정 */
    margin-left: 50px; /* 메뉴 간격 수정 */
}

.main-nav li {
    margin-right: 60px;
    position: relative;
    font-size: 25px;
    font-weight: 700;
    color: #454545;
}

.main-nav li::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -30px; /* 세로선의 오른쪽 위치 조절 */
    transform: translateY(-50%);
    width: 1px;
    height: 15px;
    background-color: lightgray;
}

.main-nav:last-child {
    margin-left: auto; /* 마지막 nav 요소를 맨 오른쪽으로 이동 */
}

.main-nav li:last-child {
    margin-right: 0; /* 마지막 li 요소의 오른쪽 간격 제거 */
}

.header-right {
    display: flex;
    flex-direction: column; /* 위아래로 배치 */
    align-items: center;
    margin-right: 30px;
    margin-top: 80px;
}

.sub-nav {
    font-size: 12px;
    margin-top: 10px; /* 위쪽 여백 추가 */
    margin-bottom: 10px;
    margin-right: -130px;
    position: relative; /* li 요소의 위치를 기준으로 세로선을 생성하기 위해 position: relative 설정 */
}

.sub-nav ul {
    display: flex; /* li 요소들을 가로로 배치 */
    list-style: none;
    padding: 0;
    margin-left: 20px;
}

.sub-nav li {
    margin-right: 18px;
    position: relative;
    font-size: 15px;
    font-weight: 500;
}

.sub-nav li:not(:last-child)::after {
    content: "";
    position: absolute;
    top: 50%;
    right: -10px; /* 세로선의 오른쪽 위치 조절 */
    transform: translateY(-50%);
    width: 1px;
    height: 10px;
    background-color: lightgray;
}

.search {
    position: relative;
    display: flex;
    margin-right: -30px;
}

.search > input {
    /* input 스타일 */
    width: 180px;
    height: 30px;
    padding-left: 40px;
    border-radius: 5px;
    border: 1px solid #999999;
}

#searchBtn {
    width: 20px;
    height: 20px;
    border: none;
    position: absolute;
    left: 7px;
    top: 6px;
    background-image: url("../../../../public/images/searchImage.png"); 
    background-size: cover;
    background-repeat: no-repeat;
    background-color: transparent;
    cursor: pointer;
}

